export default {
  name:'hl_fish_robot_config',
  data() {
    return {
      namelist:[],
      activeName: "fishing_normal",
      //三个场次总数据
      resData: {},
      //默认为最低的场次
      Data:{},
      id:0,
      keys:'',
     loading:false,
    };
  },
  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.Getfishing_robot({
      key: "fishing_robot.lua"
    });
    console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    console.log(res);
    this.resData = res
    this.namelist = Object.keys(res)
    this.Data = res[this.namelist[0]]
    
  },
  methods: {
    //保存和服务器配置
    async submit(type){
      if(type === 1){
          let { data } = await this.$http.HallFunConfig.Putfishing_robot({
              keys:this.keys,
              values:JSON.stringify(this.resData),
              id:this.id,
          });
          // console.log(data);
          if(data.code === 1 && data.msg === 'ok'){
            this.$message({
              type: "success",
              message: "保存成功!"
            });
          }else{
            this.$message({
              type: "warning",
              message: "保存失败!"
            });
          }
      }else if(type === 2){
        this.loading = true
     
       let { data } = await this.$http.HallFunConfig.Postfishing_robot({
         keys:this.keys,
         values:JSON.stringify(this.resData),
         id:this.id,
       });
      // console.log(data);
      if(data.code === 1 && data.msg === 'ok'){
        this.loading = false
        this.$message({
          type: "success",
          message: "发送服务器配置成功!"
        });
      }else {
          this.loading = false
        this.$message({
          type: "warning",
          message: "发送服务器配置失败!"
        });
      }

      }
    },

    
    
    //加入房间的添加
    addRobot(row) {
      row.push({})
    },

    //子弹切换的添加
    addFire(row) {
      row.push({})
    },

    //锁定概率添加
    addLock(row) {
      row.push({})
    },

    //加入房间删除
    delRobot(row,index) {
       this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          row.splice(index,1)
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      
      
    },

    //子弹切换删除
    delFire(row,index) {
       this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          row.splice(index,1)
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //锁定概率删除
    delLock(row,index) {
         this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          row.splice(index,1)
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //tap栏切换
    handleClick(tab, event) {
      Object.keys(this.resData).forEach((item)=>{
        if(item === tab.name){
          this.Data = this.resData[item]
        }
      })
      
    }
  }
};



